.App {
  text-align: center;
  font-size: 3em;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.page { margin: 20px;
}
.interactions {
  text-align: center;
  margin: 2rem;
}
.table {
margin: 20px 0;
}
.table-header {
display: -ms-flexbox;
display: flex;
line-height: 24px;
font-size: 16px;
padding: 0 10px; -ms-flex-pack: justify; justify-content: space-between;
}
.table-empty { margin: 200px; text-align: center; font-size: 16px;
} .table-row {

display: -ms-flexbox;

display: flex; line-height: 40px; white-space: nowrap; margin: 10px 0;
padding: 10px; background: #e3e3e3; border: 1px solid #e3e3e3;
}
.table-header > span { overflow: hidden; -o-text-overflow: ellipsis; text-overflow: ellipsis; padding: 0 5px;
}
.table-row > span { overflow: hidden; -o-text-overflow: ellipsis; text-overflow: ellipsis; padding: 0 5px;
}
.button-inline {
border-width: 0;
background: transparent;
color: inherit;
text-align: inherit; -webkit-font-smoothing: inherit; padding: 0;
font-size: inherit;
cursor: pointer;
font-weight: 700;
 }
.button-active {
border-radius: 0;
border-bottom: 1px solid #38BB6C;
}



